<template>
  <div class="app_Rffect app_content_box">
    <div class="tip">
      <p>该数据每小时更新,本统计只纳入排名有提升的数据，排名稳定不变的不计算在内</p>
    </div>

    <el-table class="app_table"
      style="width: 100%"
      v-loading="loading"
      :data="tableData"
      height="calc(100% - 134px)"
      max-height="calc(100% - 134px)"
      border
      size="medium"
      :highlight-current-row="true">

      <el-table-column prop="Title"
        label="提升位置"
        width="200">
      </el-table-column>

      <el-table-column prop="count"
        align="center"
        label="提升数量">
      </el-table-column>

      <el-table-column align="center"
        label="占比">
        <template slot-scope="scope">
          <el-progress v-if="scope.row.p==100"
            type="circle"
            :width=50
            :stroke-width=3
            color="rgb(19, 206, 102)"
            :percentage="scope.row.p"></el-progress>
          <el-progress v-else
            type="circle"
            :width=50
            :stroke-width=3
            :percentage="scope.row.p"></el-progress>
        </template>
      </el-table-column>

    </el-table>

    <div class="app_pages">
      <el-pagination @current-change="CurrentChange"
        :current-page="currentPage"
        layout="total, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'rffect',
  data () {
    return {
      loading: true,
      tableData: [],
      currentPage: 1,
      total: 0
    }
  },
  methods: {
    CurrentChange (val) {
      this.currentPage = val
      this.tableRefresh()
    },
    tableRefresh () {
      this.loading = true
      this.$http.post('/api/EffectUP', {
        userid: Util.user().id,
        page: this.currentPage
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.tableData = response.body.data
            this.total = response.body.total
          } else {
            this.$message.error('数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading = false
      })
    }
  },
  mounted () {
    setTimeout(() => {
      this.tableRefresh()
    }, 300)
  }
}
</script>



