<template>
  <div class="app_DayRecord app_content_box">
    <el-table class="app_table"
      style="width: 100%"
      v-loading="loading"
      :data="tableData"
      height="calc(100% - 50px)"
      max-height="calc(100% - 50px)"
      border
      size="medium"
      :highlight-current-row="true">

      <el-table-column type="index"
        label="序号"
        width="150">
      </el-table-column>

      <el-table-column prop="ID"
        label="ID"
        v-if=false>
      </el-table-column>

      <el-table-column prop="UserID"
        label="UserID"
        v-if=false>
      </el-table-column>

      <el-table-column prop="CreateTime"
        align="center"
        label="日期">
      </el-table-column>
      <el-table-column prop="Money"
        align="center"
        label="消费金额">
      </el-table-column>
    </el-table>

    <div class="app_pages">
      <el-pagination @current-change="CurrentChange"
        :current-page="currentPage"
        layout="total, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
import Util from '../assets/util'
export default {
  name: 'dayRecord',
  data () {
    return {
      loading: true,
      tableData: [],
      currentPage: 1,
      total: 0
    }
  },
  methods: {
    CurrentChange (val) {
      this.currentPage = val
      this.tableRefresh()
    },
    tableRefresh () {
      this.loading = true
      this.$http.post('/api/stat_score_day', {
        userid: Util.user().id,
        page: this.currentPage
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.tableData = response.body.data.map(function (item, key, ary) {
              let dateTxt = Util.timestampToTime(parseInt(item.CreateTime.replace(/[^0-9.]/g, '')))
              item.CreateTime = dateTxt
              return item
            })
            this.total = response.body.total
          } else {
            this.$message.error('数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading = false
      })
    }
  },
  mounted () {
    setTimeout(() => {
      this.tableRefresh()
    }, 300)
  }
}
</script>



