<template>
  <div class="app_KeywordList app_content_box">
    <div class="app_search clearfix">
      <div class="left">
        <el-button-group>
          <el-button type="primary"
            size="small"
            @click="dialogOpen(1,'设置分组')">设置分组</el-button>
          <el-button type="primary"
            size="small"
            v-loading="loading2"
            @click="keysstop">批量停用</el-button>
        </el-button-group>
      </div>
      <div class="app_search_box text-right">
        <el-form :inline="true"
          :model="formInline"
          size="small"
          class="demo-form-inline">
          <el-form-item label="关键词">
            <el-input v-model="formInline.key"
              placeholder="关键词"></el-input>
          </el-form-item>
          <el-form-item label="搜索引擎">
            <el-select v-model="formInline.webID"
              placeholder="搜索引擎">
              <el-option label="查看全部搜索引擎"
                value=""></el-option>
              <el-option label="百度PC"
                :value="1"></el-option>
              <el-option label="百度手机"
                :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="分组">
            <el-select v-model="formInline.groupID"
              width="100"
              placeholder="请选择分组">
              <el-option label="查看全部分组"
                value=""></el-option>
              <el-option v-for="(item, index, key) in groupAll"
                :key="key"
                :label="item.GroupName"
                :value="item.ID"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
              icon="el-icon-search"
              size="small"
              @click="onSearch">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <el-table class="app_table"
      style="width: 100%"
      v-loading="loading"
      ref="tableData"
      :data="tableData"
      @selection-change="changeFun"
      @sort-change="sortFun"
      height="calc(100% - 218px)"
      max-height="calc(100% - 218px)"
      border
      size="medium"
      :highlight-current-row="true">

      <el-table-column fixed="left"
        type="selection"
        width="55">
      </el-table-column>

      <el-table-column type="index"
        :index="typeIndex"
        label="序号"
        width="50">
      </el-table-column>

      <el-table-column prop="KeyValue"
        label="关键词"
        min-width="150">
      </el-table-column>

      <el-table-column label="分组"
        min-width="100">
        <template slot-scope="scope">
          <span v-if="groupAll.length<=0">
            未知分组
          </span>
          <span v-else
            v-for="(item, index, key) in groupAll"
            :key="key">
            <b v-if="item.ID===scope.row.GroupID">{{item.GroupName}}</b>
          </span>
        </template>
      </el-table-column>

      <el-table-column label="类型"
        min-width="100">
        <template slot-scope="scope">
          <span v-if="scope.row.WebID===1">百度PC</span>
          <span v-else-if="scope.row.WebID===2">百度移动</span>
          <span v-else>未知类型</span>
        </template>
      </el-table-column>

      <el-table-column label="域名"
        min-width="250">
        <template slot-scope="scope">
          <a :href="'http://'+scope.row.Address"
            target="_blank"
            class="buttonText">{{scope.row.Address}}</a>
        </template>
      </el-table-column>

      <el-table-column prop="BaiduIndex"
        label="指数"
        width="100">
      </el-table-column>

      <el-table-column prop="Price"
        label="日费用"
        min-width="80">
      </el-table-column>

      <el-table-column label="今日"
        min-width="80">
        <template slot-scope="scope">
          <div v-html="scope.row.TodayPrice"></div>
        </template>
      </el-table-column>

      <el-table-column label="状态"
        min-width="100">
        <template slot-scope="scope">
          <div v-html="scope.row.StatusHtml"></div>
        </template>
      </el-table-column>

      <el-table-column prop="UpdateTime"
        label="更新时间"
        min-width="100">
      </el-table-column>

      <el-table-column prop="StartRank"
        label="初排"
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="NewRank"
        label="新排"
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="EffectValue"
        label="变化"
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="CreateTime"
        label="添加时间"
        min-width="100">
      </el-table-column>

      <el-table-column prop="SuccessTime"
        label="达标时间"
        min-width="110">
      </el-table-column>

      <el-table-column prop="PayQty"
        label="扣费次数"
        min-width="110"
        sortable="custom">
      </el-table-column>

      <el-table-column fixed="right"
        label="操作"
        width="100">
        <template slot-scope="scope">
          <el-button type="text"
            size="small"
            @click="keyinfo(scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="app_pages">
      <el-pagination @current-change="CurrentChange"
        :current-page="currentPage"
        :page-size="100"
        layout="total, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <div class="tip">
      <p v-cloak>您当前的用户等级为
        <span class="font-danger">{{userData.gradeName}}</span> ,关键词最大管理数量为
        <span class="font-primary">{{userData.KeyMaxCount}}</span> 条，还可以添加
        <span class="font-warning">{{userData.KeyMaxCount-userData.KeyCount}}</span> 条</p>
    </div>

    <el-dialog :title="dialogTitle"
      width="30%"
      append-to-body
      @close="closeDialog"
      :visible.sync="dialogBtn">
      <el-form :model="btnForm"
        :rules="rules"
        ref="btnForm">
        <el-form-item v-if="catGroup ===1"
          label="设置分组"
          :label-width="formLabelWidth"
          prop="info">
          <el-select v-model="btnForm.info"
            style="width:100%"
            placeholder="请选择分组">
            <el-option v-for="(item, index, key) in groupAll"
              :key="key"
              :label="item.GroupName"
              :value="item.ID"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button type="primary"
          @click.native.prevent="submitG('btnForm')"
          v-loading="loading3">提 交</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'keywordList',
  props: ['userData'],
  data () {
    return {
      loading: true,
      loading2: false,
      loading3: false,
      tableData: [],
      tableDESC: 11,
      currentPage: 1,
      total: 0,
      groupAll: [],
      selectID: [],
      formInline: {
        key: '',
        webID: '',
        groupID: ''
      },
      dialogBtn: false,
      dialogTitle: '设置分组',
      catGroup: 1,
      formLabelWidth: '120px',
      btnForm: {
        info: ''
      },
      rules: {
        info: [{ required: true, message: '请选择', trigger: 'blur' }]
      }
    }
  },
  methods: {
    onSearch () {
      this.tableRefresh()
    },
    typeIndex (index) {
      return index + (this.currentPage - 1) * 10 + 1
    },
    CurrentChange (val) {
      this.currentPage = val
      this.tableRefresh()
    },
    tableRefresh () {
      this.loading = true
      this.$http.post('/api/keys', {
        userid: Util.user().id,
        page: this.currentPage,
        groupID: this.formInline.groupID,
        webID: this.formInline.webID,
        orderType: this.tableDESC,
        key: this.formInline.key
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.tableData = response.body.data.map(function (item, key, ary) {
              for (let dateI in item) {
                if (typeof item[dateI] === 'string' && item[dateI].indexOf('/Date(') !== -1) {
                  item[dateI] = Util.timestampToTime(parseInt(item[dateI].replace(/[^0-9.]/g, '')))
                }
              }
              return item
            })
            this.total = response.body.total
          } else {
            this.$message.error('数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading = false
      })
    },
    selectInfo () {
      this.$http.post('/api/GroupALL', {
        userid: Util.user().id
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.groupAll = response.body
          } else {
            this.$message.error('分组数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('分组数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      })
    },
    keyinfo (data) {
      console.log(data)
    },
    changeFun (val) {
      let arr = val.map(function (item, index, arr) {
        return item.ID
      })
      this.selectID = arr.join(',')
    },
    sortFun (column) {
      console.log(column)
      if (column.prop === 'StartRank') {
        column.order === 'ascending' ? this.tableDESC = 15 : this.tableDESC = 16
      } else if (column.prop === 'NewRank') {
        column.order === 'ascending' ? this.tableDESC = 11 : this.tableDESC = 12
      } else if (column.prop === 'EffectValue') {
        column.order === 'ascending' ? this.tableDESC = 14 : this.tableDESC = 13
      } else if (column.prop === 'SuccessFlag') {
        column.order === 'ascending' ? this.tableDESC = 18 : this.tableDESC = 17
      } else if (column.prop === 'PayQty') {
        column.order === 'ascending' ? this.tableDESC = 20 : this.tableDESC = 19
      } else {
        this.tableDESC = 11
      }
      this.tableRefresh()
    },
    keysstop () {
      if (this.selectID.length <= 0) {
        this.$message({
          message: '请勾选后操作',
          type: 'warning',
          duration: 800
        })
        return false
      }
      this.loading2 = true
      this.$http.post('/api/keysstop', {
        userid: Util.user().id,
        ids: this.selectID
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.$message({
              message: '批量停用成功',
              type: 'success',
              duration: 800
            })
            this.tableRefresh()
          } else {
            this.$message.error('停用失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('停用失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading2 = false
      })
    },
    submitG (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let url, json
          if (this.catGroup === 1) {
            url = '/api/setkeysgroup'
            json = {
              userid: Util.user().id,
              groupID: this.btnForm.info,
              ids: this.selectID
            }
          } else if (this.catGroup === 2) {
            url = '/api/setkeyscat'
            json = {
              userid: Util.user().id,
              catID: this.btnForm.info,
              ids: this.selectID
            }
          } else {
            return false
          }
          this.loading3 = true
          this.$http.post(url, json).then(
            response => {
              if (response.status === 200 && response.ok) {
                this.$message({
                  message: '设置成功',
                  type: 'success',
                  duration: 800
                })
                this.tableRefresh()
                this.closeDialog()
              } else {
                this.$message.error('设置失败，请刷新重试')
              }
            },
            response => {
              this.$message.error('设置失败，请刷新重试')
            }
          ).catch(response => {
            console.log(response)
          }).finally(() => {
            this.loading3 = false
          })
        } else {
          return false
        }
      })
    },
    dialogOpen (num, title) {
      if (this.selectID <= 0) {
        this.$message({
          message: '请勾选后操作',
          type: 'warning',
          duration: 800
        })
      } else {
        this.dialogBtn = true
        this.catGroup = num
        this.dialogTitle = title
      }
    },
    closeDialog () {
      this.$refs.btnForm.clearValidate()
      this.btnForm.info = ''
      this.dialogBtn = false
    }
  },
  mounted: function () {
    this.selectInfo()
    setTimeout(() => {
      this.tableRefresh()
    }, 300)
  }
}
</script>



