<template>
  <div class="app_index">
    <el-container class="app_container">
      <el-header>
        <Header @asideOff="asideOff"
          :userData="userData"></Header>
      </el-header>
      <el-container class="app_main">
        <el-aside width="auto">
          <Aside :asideSwitch="aside"></Aside>
        </el-aside>
        <el-main>
          <Breadcrumb></Breadcrumb>
          <!-- <transition enter-active-class="bouncelnRight"
            leave-active-class="bounceOutLeft"></transition> -->
          <el-collapse-transition>
            <router-view :userData="userData"
              class="animated"></router-view>
          </el-collapse-transition>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from '../components/Header'
import Aside from '../components/Aside'
import Breadcrumb from '../components/Breadcrumb'
import Util from '../assets/util'

export default {
  name: 'appIndex',
  data () {
    return {
      aside: false,
      userData: {}
    }
  },
  components: {
    Header,
    Aside,
    Breadcrumb
  },
  methods: {
    asideOff (data) {
      this.aside = data
    },
    getUser () {
      this.$http.post('/api/UserInfo', {
        userid: Util.user().id
      }).then(response => {
        if (response.status === 200 && response.ok) {
          this.userData = response.body
        } else {
          this.$message.error('请求用户信息失败，请刷新重试')
        }
      }, response => {
        this.$message.error('请求用户信息失败，请刷新重试')
      }).catch(response => {
        console.log(response)
      })
    }
  },
  beforeMount () {
    this.getUser()
  }
}
</script>



