<template>
  <div class="app_login">
    <div class="app_login_mask"></div>
    <el-form :model="ruleForm"
      :rules="rules2"
      ref="ruleForm"
      label-position="left"
      label-width="0px"
      class="demo-ruleForm login-container">

      <h3 class="title">会员登录</h3>

      <el-form-item prop="account">
        <el-input type="text"
          v-model="ruleForm.account"
          auto-complete="off"
          placeholder="用户名"></el-input>
      </el-form-item>

      <el-form-item prop="pass">
        <el-input type="password"
          v-model="ruleForm.pass"
          auto-complete="off"
          placeholder="密码"></el-input>
      </el-form-item>

      <el-form-item prop="verifycode">
        <el-input type="text"
          v-model="ruleForm.verifycode"
          auto-complete="off"
          placeholder="验证码">
          <img :src="verifycodeUrl"
            alt="验证码"
            style="width: 90px;height: 80%;margin-top: 4%;cursor: pointer;"
            slot="suffix"
            @click="getCodeKey">
        </el-input>
      </el-form-item>

      <el-checkbox v-model="checked"
        class="remember">记住账号</el-checkbox>

      <el-form-item style="width:100%;">
        <el-button type="primary"
          style="width:100%;"
          @click.native.prevent="handleSubmit2"
          :loading="logining">登录</el-button>
      </el-form-item>

      <el-form-item class="text-right register">
        <span class="text">还没有账号？立即</span>
        <router-link to="/register"
          class="buttonText">前往注册</router-link>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Api from '../api/api'
export default {
  name: 'login',
  data () {
    return {
      logining: false,
      verifycodeUrl: '',
      verifycodeKey: '',
      ruleForm: {
        account: sessionStorage.getItem('sessionA') ? decodeURI(sessionStorage.getItem('sessionA')) : '',
        pass: sessionStorage.getItem('sessionB') ? decodeURI(sessionStorage.getItem('sessionB')) : '',
        verifycode: ''
      },
      checked: true,
      rules2: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { min: 4, message: '最少 4 个字符', trigger: 'blur' }
        ],
        pass: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, message: '最少 6 个字符', trigger: ['blur', 'change'] }
        ],
        verifycode: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 4, max: 4, message: '验证码为 4 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    verifycodeKey (val) {
      this.verifycodeUrl = Api + '/verifycode?key=' + val
    }
  },
  methods: {
    handleSubmit2 () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.logining = true
          this.$http.post('/api/Login', {
            username: this.ruleForm.account,
            password: this.ruleForm.pass,
            checkCode: this.ruleForm.verifycode,
            key: this.verifycodeKey
          }).then(response => {
            if (response.status === 200 && response.ok) {
              if (response.body.indexOf('1') !== -1) {
                this.$message({
                  message: '登陆成功',
                  type: 'success',
                  duration: 800
                })
                if (this.checked) {
                  sessionStorage.setItem('sessionA', encodeURI(this.ruleForm.account))
                } else {
                  sessionStorage.removeItem('sessionA')
                }
                sessionStorage.setItem('sessionID', encodeURI('看-' + Math.random() + '你-' + response.body.split('|')[1] + '妹'))
                setTimeout(() => {
                  this.$router.push({ path: '/membercenter' })
                }, 600)
              } else if (response.body === '2') {
                this.$message({
                  message: '账号或密码有误',
                  type: 'warning'
                })
                this.ruleForm.pass = ''
              } else if (response.body === '3') {
                this.$message({
                  message: '验证码错误',
                  type: 'warning'
                })
                this.ruleForm.verifycode = ''
              } else {
                this.$message.error('请求失败，请重试')
              }
            }
          }, response => {
            this.$message.error('请求失败，请重试')
          }).catch(response => {
            console.log(response)
          }).finally(() => {
            this.logining = false
          })
        } else {
          return false
        }
      })
    },
    getCodeKey () {
      this.$http.get('/api/GetVerifyCode').then(response => {
        this.verifycodeKey = response.body
      }).catch(response => {
        console.log(response)
      })
    }
  },
  mounted () {
    sessionStorage.removeItem('sessionB')
    this.getCodeKey()
  }
}
</script>

<style scoped>
.login-container .remember {
  margin: 0px 0px 35px 0px;
}
</style>



