<template>
  <div class="app_dialog">
    <el-dialog class="headerdialog"
      width="40%"
      title="修改密码"
      append-to-body
      @close="closeDialogSun('form')"
      :visible.sync="dialogStatus">
      <el-form :model="form"
        status-icon
        :rules="rules"
        ref="form"
        v-loading="proceed"
        element-loading-background="rgba(255, 255, 255, 0.5)"
        :label-width="formLabelWidth">
        <el-form-item label="原密码："
          prop="oldplass">
          <el-input v-model.trim="form.oldplass"
            auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="新密码："
          prop="pass">
          <el-input :type="form.newplasswordType"
            v-model.trim="form.pass"
            auto-complete="off">
            <i slot="prefix"
              class="el-input__icon el-icon-view"
              @mousedown="passwordShow(1)"
              @mouseup="passwordShow(2)"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="确认密码："
          prop="checkPass">
          <el-input :type="form.newplassword2Type"
            v-model.trim="form.checkPass"
            auto-complete="off">
            <i slot="prefix"
              class="el-input__icon el-icon-view"
              @mousedown="passwordShow(3)"
              @mouseup="passwordShow(4)"></i>
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button @click="closeDialogSun('form')"
          :disabled="proceed">取 消</el-button>
        <el-button type="primary"
          @click="submitForm('form')"
          :disabled="proceed">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Util from '../assets/util'
export default {
  name: 'headerdagilog',
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.form.checkPass !== '') {
          this.$refs.form.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      dialogStatus: false,
      proceed: false,
      formLabelWidth: '120px',
      form: {
        oldplass: '',
        pass: '',
        newplasswordType: 'password',
        checkPass: '',
        newplassword2Type: 'password'
      },
      rules: {
        oldplass: [
          { required: true, message: '请输入原密码', trigger: 'blur' },
          { min: 6, message: '最少 6 个字符', trigger: ['blur', 'change'] }
        ],
        pass: [
          { min: 6, message: '最少 6 个字符', trigger: ['blur', 'change'] },
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  watch: {
    dialogStatus () {
      this.dialogStatus = this.dialogOffSun
    },
    dialogOffSun (val) {
      this.dialogStatus = val
    }
  },
  props: ['dialogOffSun'],
  methods: {
    closeDialogSun (formName) {
      if (formName) this.$refs[formName].clearValidate()
      this.form.oldplass = ''
      this.form.pass = ''
      this.form.checkPass = ''
      this.proceed = false
      this.dialogStatus = false
      this.$emit('dialogData', false)
    },
    passwordShow (n) {
      if (n === 1 && this.newplassword !== '') this.form.newplasswordType = 'text'
      if (n === 2 && this.newplassword !== '') this.form.newplasswordType = 'password'
      if (n === 3 && this.newplassword2 !== '') this.form.newplassword2Type = 'text'
      if (n === 4 && this.newplassword2 !== '') this.form.newplassword2Type = 'password'
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.proceed = true
          this.$http.post('/api/modifypass', {
            userid: Util.user().id,
            oldPassword: this.form.oldplass,
            password: this.form.pass
          }).then(response => {
            if (response.status === 200 && response.ok) {
              if (response.body === 1) {
                this.$message({
                  message: '修改密码成功，请重新登陆',
                  type: 'success',
                  duration: 800
                })
                this.closeDialogSun(formName)
                setTimeout(() => {
                  this.$router.push({ path: '/login' })
                }, 600)
              } else if (response.body === 2) {
                this.$message({
                  message: '原密码不正确',
                  type: 'warning',
                  duration: 800
                })
              } else {
                this.$message.error('请求失败，请重试')
              }
            }
          }, response => {
            this.$message.error('请求失败，请重试')
          }).finally(() => {
            this.proceed = false
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style scoped>
.headerdialog .el-icon-view {
  cursor: pointer;
}

.headerdialog .el-icon-view:hover {
  color: #909399;
}
</style>



