<template>
  <div class="app_header clearfix">
    <div class="left">
      <i class="el-icon-menu app_menu"
        :title="asideOff"
        @click="operateAside()"></i>
      <!-- <el-button type="primary" icon="el-icon-menu" circle></el-button> -->
    </div>
    <div class="right">
      <el-menu class="el-menu-demo"
        mode="horizontal"
        menu-trigger="click"
        :default-active="activeIndex">
        <!-- <el-menu-item index="1">
          <el-badge :value="message"
            :max="10"
            class="item app_badge">消息</el-badge>
        </el-menu-item> -->
        <el-submenu index="2">
          <template slot="title"
            v-cloak>{{userData.Account}}</template>
          <el-menu-item index="2-1"
            @click="openDialog()">修改密码</el-menu-item>
          <el-menu-item index="2-2"
            class="app_exit"
            @click="exit">
            退出
            <i class="el-icon-arrow-right right"></i>
          </el-menu-item>
        </el-submenu>
      </el-menu>
    </div>
    <HeaderDagilog :dialogOffSun="dialogOff"
      @dialogData="closeDialog"></HeaderDagilog>
  </div>
</template>
<script>
import HeaderDagilog from './HeaderDialog'
export default {
  name: 'appHeader',
  components: {
    HeaderDagilog
  },
  props: ['userData'],
  data () {
    return {
      message: 6,
      username: '123123',
      activeIndex: '2-1',
      dialogOff: false,
      asideOff: false
    }
  },
  methods: {
    operateAside () {
      this.asideOff = !this.asideOff
      this.$emit('asideOff', this.asideOff)
    },
    openDialog () {
      this.dialogOff = true
    },
    closeDialog (data) {
      this.dialogOff = data
    },
    exit () {
      this.$confirm('确认退出吗？').then(() => {
        sessionStorage.removeItem('sessionUser')
        this.$message({
          message: '退出成功',
          type: 'success',
          duration: 500
        })
        setTimeout(() => {
          this.$router.push({ path: '/login' })
        }, 500)
      }).catch(() => { })
    }
  }
}
</script>



