<template>
  <div class="app_MemberCenter">
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="tip">
          <p v-cloak>欢迎您：{{userData.Account}}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="demo-color-box bg-success">
          <div class="value">会员等级</div>
          <div v-cloak>{{userData.gradeName}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="demo-color-box bg-warning">
          <div class="value">余额</div>
          <div v-cloak>{{userData.Amount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="demo-color-box bg-danger">
          <div class="value">关键词数量</div>
          <div v-cloak>{{userData.KeyCount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="demo-color-box bg-info">
          <div class="value">手机</div>
          <div v-cloak>{{phone}}</div>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="info_title">
          <h3>温馨提示：</h3>
          <p>还没有手机认证的用户请于本页面手机未认证链接处进行认证，通过手机认证的用户，将会获得积分续费短信提醒功能</p>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'memberCenter',
  props: ['userData'],
  data () {
    return {}
  },
  computed: {
    phone () {
      return this.userData.Mobile ? this.userData.Mobile.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2') : ''
    }
  }
}
</script>
<style scoped>
.demo-color-box {
  border-radius: 4px;
  padding: 20px;
  margin: 5px 0;
  box-sizing: border-box;
  color: #fff;
  font-size: 16px;
}
.demo-color-box .value {
  font-size: 14px;
  opacity: 0.69;
  line-height: 30px;
}
.info_title h3 {
  margin-top: 60px;
}
</style>



