<template>
  <div class="app_login app_register">
    <div class="app_login_mask"></div>
    <el-form :model="registerForm"
      :rules="rules"
      ref="registerForm"
      class="demo-ruleForm login-container">
      <h3 class="title">会员注册</h3>

      <el-form-item prop="account">
        <el-input type="text"
          v-model.trim="registerForm.account"
          auto-complete="off"
          prefix-icon="el-icon-edit"
          placeholder="登录名"></el-input>
      </el-form-item>

      <el-form-item prop="pass">
        <el-input type="password"
          v-model.trim="registerForm.pass"
          auto-complete="off"
          prefix-icon="el-icon-view"
          clearable
          placeholder="密码"></el-input>
      </el-form-item>

      <el-form-item prop="checkPass">
        <el-input type="password"
          v-model.trim="registerForm.checkPass"
          auto-complete="off"
          prefix-icon="el-icon-view"
          clearable
          placeholder="确认密码"></el-input>
      </el-form-item>

      <el-form-item prop="truename">
        <el-input type="text"
          v-model.trim="registerForm.truename"
          auto-complete="off"
          prefix-icon="el-icon-edit"
          placeholder="姓名"></el-input>
      </el-form-item>

      <el-form-item prop="email">
        <el-input type="text"
          v-model.trim="registerForm.email"
          auto-complete="off"
          prefix-icon="el-icon-message"
          placeholder="邮箱"></el-input>
      </el-form-item>

      <el-form-item prop="qq">
        <el-input type="text"
          v-model.trim="registerForm.qq"
          auto-complete="off"
          prefix-icon="el-icon-share"
          placeholder="QQ"></el-input>
      </el-form-item>

      <el-form-item prop="phone">
        <el-input type="phone"
          v-model.trim="registerForm.phone"
          auto-complete="off"
          prefix-icon="el-icon-phone"
          clearable
          placeholder="联系电话"></el-input>
      </el-form-item>

      <el-form-item style="width:100%;">
        <el-button type="primary"
          style="width:100%;"
          @click.native.prevent="register"
          :loading="logining">注册</el-button>
      </el-form-item>

      <el-form-item class="text-right register">
        <span class="text">已有账号？立即 </span>
        <router-link to="/login"
          class="buttonText">前往登陆</router-link>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
import Util from '../assets/util'
export default {
  name: 'register',
  data () {
    let checkPass = (rules, value, callback) => {
      Util.validate.checkPass({ rules, value, callback }, this.registerForm.pass)
    }

    return {
      logining: false,
      registerForm: {
        account: '',
        pass: '',
        checkPass: '',
        phone: '',
        truename: '',
        email: '',
        qq: ''
      },
      rules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { min: 4, message: '最少 4 个字符', trigger: 'blur' },
          { validator: Util.validate.namePass, trigger: 'blur' }
        ],
        pass: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, message: '最少 6 个字符', trigger: ['blur', 'change'] },
          { validator: Util.validate.namePass, trigger: ['blur', 'change'] }
        ],
        checkPass: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { validator: checkPass, trigger: ['blur', 'change'] }
        ],
        phone: [
          { required: true, message: '请输入联系电话', trigger: 'blur' },
          { validator: Util.validate.phone, trigger: ['blur', 'change'] }
        ],
        truename: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { validator: Util.validate.truename, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ],
        qq: [
          { validator: Util.validate.qq, trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  methods: {
    register () {
      this.$refs.registerForm.validate((valid) => {
        if (valid) {
          this.logining = true
          this.$http.post('/api/Reg', {
            account: this.registerForm.account,
            password: this.registerForm.pass,
            truename: this.registerForm.truename,
            email: this.registerForm.email,
            mobile: this.registerForm.phone,
            qq: this.registerForm.email
          }).then(response => {
            if (response.status === 200 && response.ok) {
              if (response.body === 1) {
                this.$message({
                  message: '恭喜你，注册成功，前去登陆',
                  type: 'success',
                  duration: 800
                })
                sessionStorage.setItem('sessionA', encodeURI(this.registerForm.account))
                sessionStorage.setItem('sessionB', encodeURI(this.registerForm.pass))
                setTimeout(() => {
                  this.$router.push({ path: '/login' })
                }, 600)
              } else if (response.body === 2) {
                this.$message({
                  message: '很抱歉，该信息已被注册',
                  type: 'warning'
                })
              } else {
                this.$message.error('请求失败，请重试')
              }
            }
          }, response => {
            this.$message.error('请求失败，请重试')
          }).catch(response => {
            console.log(response)
          }).finally(() => {
            this.logining = false
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>



