import Vue from 'vue'
import Router from 'vue-router'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'
import Index from '../views/Index.vue'
import MemberCenter from '../views/MemberCenter.vue'
import GroupingManagement from '../views/GroupingManagement.vue'
import KeywordList from '../views/KeywordList.vue'
import AddKeyword from '../views/AddKeyword.vue'
import RecycleBin from '../views/RecycleBin.vue'
import Rffect from '../views/Rffect.vue'
import DayRecord from '../views/DayRecord.vue'
import MonthRecord from '../views/MonthRecord.vue'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [{
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  }, {
    path: '/register',
    component: Register,
    name: '',
    hidden: true
  }, {
    path: '/404',
    component: Register,
    name: '',
    hidden: true
  }, {
    path: '/',
    redirect: '/membercenter'
  }, {
    path: '/',
    component: Index,
    name: '关键词管理',
    children: [{
      path: '/groupingmanagement',
      component: GroupingManagement,
      name: '分组管理'
    }, {
      path: '/keywordlist',
      component: KeywordList,
      name: '关键词列表'
    }, {
      path: '/addkeyword',
      component: AddKeyword,
      name: '新增词'
    }, {
      path: '/recyclebin',
      component: RecycleBin,
      name: '回收站'
    }]
  }, {
    path: '/',
    component: Index,
    name: '统计数据',
    children: [{
      path: '/rffect',
      component: Rffect,
      name: '效果分析'
    }, {
      path: '/dayrecord',
      component: DayRecord,
      name: '日消费记录'
    }, {
      path: '/monthrecord',
      component: MonthRecord,
      name: '月消费记录'
    }]
  }, {
    path: '/',
    component: Index,
    name: '会员中心',
    children: [{
      path: '/membercenter',
      component: MemberCenter,
      name: '我的中心'
    }]
  }, {
    path: '*',
    hidden: true,
    redirect: {
      path: '/404'
    }
  }]
})



