<template>
  <div class="app_aside">
    <el-menu class="el-menu-vertical-demo"
      mode="vertical"
      :collapse="asideSwitch"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#66b1ff"
      :default-active="$route.path.substring(1)"
      unique-opened
      menu-trigger="click"
      router
      collapse-transition>
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-setting"></i>
          <span slot="title">关键词管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="groupingmanagement">分组管理</el-menu-item>
          <el-menu-item index="keywordlist">关键词列表</el-menu-item>
          <el-menu-item index="addkeyword">新增词</el-menu-item>
          <el-menu-item index="recyclebin">回收站</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-document"></i>
          <span slot="title">统计数据</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="rffect">效果分析</el-menu-item>
          <el-menu-item index="dayrecord">日消费记录</el-menu-item>
          <el-menu-item index="monthrecord">月消费记录</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-star-on"></i>
          <span slot="title">会员中心</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="membercenter">我的中心</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'appAside',
  props: ['asideSwitch'],
  data () {
    return {}
  }
}
</script>



