<template>
  <div class="app_RecycleBin app_content_box">
    <div class="app_search clearfix">
      <div class="left">
        <el-button-group>
          <el-button type="primary"
            size="small"
            v-loading="loading2"
            @click="keysChange(1,'批量开启')">批量开启</el-button>
          <el-button type="primary"
            size="small"
            v-loading="loading3"
            @click="keysChange(2,'批量删除')">批量删除</el-button>
        </el-button-group>
      </div>
      <div class="app_search_box text-right">
        <el-form :inline="true"
          :model="formInline"
          size="small"
          class="demo-form-inline">
          <el-form-item label="关键词">
            <el-input v-model="formInline.key"
              placeholder="关键词"></el-input>
          </el-form-item>
          <el-form-item label="搜索引擎">
            <el-select v-model="formInline.webID"
              placeholder="搜索引擎">
              <el-option label="查看全部搜索引擎"
                value=""></el-option>
              <el-option label="百度PC"
                value="1"></el-option>
              <el-option label="百度手机"
                value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="分组">
            <el-select v-model="formInline.groupID"
              width="100"
              placeholder="请选择分组">
              <el-option label="查看全部分组"
                value=""></el-option>
              <el-option v-for="(item, index, key) in groupAll"
                :key="key"
                :label="item.GroupName"
                :value="item.ID"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
              icon="el-icon-search"
              size="small"
              @click="onSearch">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <el-table class="app_table"
      style="width: 100%"
      v-loading="loading"
      :data="tableData"
      @selection-change="changeFun"
      @sort-change="sortFun"
      height="calc(100% - 218px)"
      max-height="calc(100% - 218px)"
      border
      size="medium"
      :highlight-current-row="true">
      <el-table-column fixed="left"
        type="selection"
        width="55">
      </el-table-column>

      <el-table-column type="index"
        :index="typeIndex"
        label="序号"
        width="50">
      </el-table-column>

      <el-table-column prop="KeyValue"
        label="关键词"
        min-width="150">
      </el-table-column>

      <el-table-column label="分组"
        min-width="100">
        <template slot-scope="scope">
          <span v-if="groupAll.length<=0">
            未知分组
          </span>
          <span v-else
            v-for="(item, index, key) in groupAll"
            :key="key">
            <b v-if="item.ID===scope.row.GroupID">{{item.GroupName}}</b>
          </span>
        </template>
      </el-table-column>

      <el-table-column label="类型"
        min-width="100">
        <template slot-scope="scope">
          <span v-if="scope.row.WebID===1">百度PC</span>
          <span v-else-if="scope.row.WebID===2">百度移动</span>
          <span v-else>未知类型</span>
        </template>
      </el-table-column>

      <el-table-column label="域名"
        min-width="250">
        <template slot-scope="scope">
          <a :href="'http://'+scope.row.Address"
            target="_blank"
            class="buttonText">{{scope.row.Address}}</a>
        </template>
      </el-table-column>

      <el-table-column prop="BaiduIndex"
        label="指数"
        width="100">
      </el-table-column>

      <el-table-column label="状态"
        min-width="100">
        <template slot-scope="scope">
          <div v-html="scope.row.StatusHtml"></div>
        </template>
      </el-table-column>

      <el-table-column prop="StartRank"
        label="初排"
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="NewRank"
        label="新排"
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="CreateTime"
        label="添加时间"
        min-width="100">
      </el-table-column>
    </el-table>

    <div class="app_pages">
      <el-pagination @current-change="CurrentChange"
        :current-page="currentPage"
        :page-size="100"
        layout="total, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <div class="tip">
      <p v-cloak>本平台根据用户等级限量开启百度PC的无排名服务模式，您当前的用户等级总共可提供
        <span class="font-danger">{{userData.NoRankCount}}</span>个服务名额。点击状态栏下的无排名链接开启。
      </p>
    </div>
  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'recycleBin',
  props: ['userData'],
  data () {
    return {
      loading: true,
      loading2: false,
      loading3: false,
      tableData: [],
      tableDESC: 11,
      currentPage: 1,
      total: 0,
      groupAll: [],
      selectID: [],
      formInline: {
        key: '',
        webID: '',
        groupID: ''
      }
    }
  },
  methods: {
    onSearch () {
      this.tableRefresh()
    },
    typeIndex (index) {
      return index + (this.currentPage - 1) * 10 + 1
    },
    CurrentChange (val) {
      this.currentPage = val
      this.tableRefresh()
    },
    tableRefresh () {
      this.loading = true
      this.$http.post('/api/keys', {
        userid: Util.user().id,
        page: this.currentPage,
        groupID: this.formInline.groupID,
        webID: this.formInline.webID,
        orderType: this.tableDESC,
        key: this.formInline.key,
        status: 2
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.tableData = response.body.data.map(function (item, key, ary) {
              for (let dateI in item) {
                if (typeof item[dateI] === 'string' && item[dateI].indexOf('/Date(') !== -1) {
                  item[dateI] = Util.timestampToTime(parseInt(item[dateI].replace(/[^0-9.]/g, '')))
                }
              }
              return item
            })
            this.total = response.body.total
          } else {
            this.$message.error('数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading = false
      })
    },
    selectInfo () {
      this.$http.post('/api/GroupALL', {
        userid: Util.user().id
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.groupAll = response.body
          } else {
            this.$message.error('分组数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('分组数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      })
    },
    changeFun (val) {
      let arr = val.map(function (item, index, arr) {
        return item.ID
      })
      this.selectID = arr.join(',')
    },
    sortFun (column) {
      console.log(column)
      if (column.prop === 'StartRank') {
        column.order === 'ascending' ? this.tableDESC = 15 : this.tableDESC = 16
      } else if (column.prop === 'NewRank') {
        column.order === 'ascending' ? this.tableDESC = 11 : this.tableDESC = 12
      } else {
        this.tableDESC = 11
      }
      this.tableRefresh()
    },
    keysChange (n, msg) {
      if (this.selectID.length <= 0) {
        this.$message({
          message: '请勾选后操作',
          type: 'warning',
          duration: 800
        })
        return false
      }
      let url
      if (n === 1) {
        this.loading2 = true
        url = '/api/keysopen'
      } else if (n === 2) {
        this.loading3 = true
        url = '/api/keysdel'
      } else {
        return false
      }
      this.$http.post(url, {
        userid: Util.user().id,
        ids: this.selectID
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.$message({
              message: msg + '成功',
              type: 'success',
              duration: 800
            })
            this.tableRefresh()
          } else {
            this.$message.error(msg + '失败，请刷新重试')
          }
        },
        response => {
          this.$message.error(msg + '失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        if (n === 1) {
          this.loading2 = false
        } else if (n === 2) {
          this.loading3 = false
        }
      })
    }
  },
  mounted: function () {
    this.selectInfo()
    setTimeout(() => {
      this.tableRefresh()
    }, 300)
  }
}
</script>



