<template>
  <div class="app_AddKeyword">
    <el-row :gutter="20">
      <el-col :xs="24"
        :sm="22"
        :md="20"
        :lg="18"
        :xl="16">
        <el-form :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm add_form">

          <el-form-item label="所属分组"
            prop="gid">
            <el-select v-model="ruleForm.gid"
              placeholder="请选择分组">
              <el-option v-for="(item, index, key) in groupAll"
                :key="key"
                :label="item.GroupName"
                :value="item.ID"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="类型"
            prop="webID">
            <el-radio-group v-model="ruleForm.webID">
              <el-radio :label="1">百度PC</el-radio>
              <el-radio :label="2">百度移动</el-radio>
            </el-radio-group>
          </el-form-item>

          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="关键词"
                prop="key">
                <el-input class="addTextarea"
                  type="textarea"
                  v-model.trim="ruleForm.key"></el-input>
                <span class="form_hint">每行一个关键词，与域名对应</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="域名"
                prop="address">
                <el-input class="addTextarea"
                  type="textarea"
                  v-model.trim="ruleForm.address"></el-input>
                <span class="form_hint">域名可以是主域名（如：baidu.com），也可以是2级域名（如：zhidao.baidu.com）</span>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="特征词">
            <el-input v-model="ruleForm.feature"></el-input>
            <span class="form_hint">如果通过关键词和域名可能出现2个以上目标结果时，将使用特征词对搜索页面的标题或描述内容进行识别。一般不用填写。</span>
          </el-form-item>

          <el-form-item>
            <el-button type="primary"
              v-loading="loading"
              @click="submitForm('ruleForm')">提 交</el-button>
            <el-button @click="resetForm('ruleForm')">重 置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <div class="tip">
      <p v-cloak>
        您当前的用户等级为
        <span class="font-danger">{{userData.gradeName}}</span>,关键词最大管理数量为
        <span class="font-primary">{{userData.KeyMaxCount}}</span> 条，还可以提交
        <span class="font-warning">{{userData.KeyMaxCount-userData.KeyCount}}</span>条 <br/> 请确保您所提交的关键词排名在百度前5页(前3页效果更好)，以免造成不必要的资源浪费。
        <br/> 为了防止用户注册小号恶意提交，用户必须至少预充值200元才能提交关键词。本平台承诺无效退款，请用户放心使用。
      </p>
    </div>
  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'addKeyword',
  props: ['userData'],
  data () {
    let keyUrl = (rules, value, callback) => {
      Util.validate.keyUrl({ rules, value, callback }, this.keyArr, this.urlArr)
    }

    return {
      loading: false,
      groupAll: [],
      ruleForm: {
        gid: '',
        webID: 1,
        key: '',
        address: '',
        feature: ''
      },
      rules: {
        gid: [
          { required: true, message: '请选择所属分组', trigger: 'change' }
        ],
        webID: [
          { required: true, message: '请选择搜索引擎', trigger: 'change' }
        ],
        address: [
          { required: true, message: '请填写域名', trigger: 'blur' },
          { validator: keyUrl, trigger: 'blur' }
        ],
        key: [
          { required: true, message: '请填写关键词', trigger: 'blur' },
          { validator: keyUrl, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    keyArr () {
      return this.ruleForm.key.split('\n').map(function (n, i, arr) {
        return n.trim()
      }).filter(function (n, i, arr) {
        return n !== ''
      })
    },
    urlArr () {
      return this.ruleForm.address.split('\n').map(function (n, i, arr) {
        return n.trim()
      }).filter(function (n, i, arr) {
        return n !== ''
      })
    },
    keyUrl () {
      return this.keyArr.map((n, i, arr) => {
        return n + '\t' + this.urlArr[i] + '\n'
      }).join('')
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.loading = true
          for (let i = 0; i < this.keyArr.length; i++) {
            this.submitApi(this.keyArr[i] + '\t' + this.urlArr[i] + '\n')
          }
          setTimeout(() => {
            this.loading = false
            this.$message({
              message: '添加成功',
              type: 'success',
              duration: 800
            })
            this.ruleForm.feature = ''
            this.resetForm(formName)
          }, this.keyArr.length * 200)
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.ruleForm.feature = ''
      this.$refs[formName].resetFields()
    },
    selectInfo () {
      this.$http.post('/api/GroupALL', {
        userid: Util.user().id
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.groupAll = response.body
          } else {
            this.$message.error('分组数据请求失败，请刷新重试')
          }
        },
        response => {
          this.$message.error('分组数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      })
    },
    submitApi (mykey) {
      this.$http.post('/api/keyadd', {
        userid: Util.user().id,
        key: mykey,
        feature: this.ruleForm.feature,
        webID: this.ruleForm.webID,
        gid: this.ruleForm.gid
      }).then(
        response => {
          // if (response.status === 200 && response.ok) {
          //   if (response.body === 1) {
          //     this.$message({
          //       message: '添加成功',
          //       type: 'success',
          //       duration: 800
          //     })
          //   } else {
          //     this.$message({
          //       message: '添加失败',
          //       type: 'warning',
          //       duration: 800
          //     })
          //   }
          // } else {
          //   this.$message.error('数据请求失败，请刷新重试')
          // }
        },
        response => {
          this.$message.error('数据请求失败，请刷新重试')
        }
      ).catch(response => {
        console.log(response)
      })
    }
  },
  mounted: function () {
    this.selectInfo()
  }
}
</script>



